import  firebase  from 'firebase';


import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';
import { User } from './../../app/models/user.model';
import { AngularFireAuth } from 'angularfire2/auth';
import { Message } from './../../app/models/message.model';
import { TabsPage } from '../tabs/tabs';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';


@IonicPage()
@Component({
  selector: 'page-conversation',
  templateUrl: 'conversation.html',
})
export class ConversationPage {
  friend : User;
  msg: string;
  messages: Message[] ;
  user : User;
  msgSub;

  constructor(private db: AngularFireDatabase,private fire: AngularFireAuth, public navCtrl: NavController, public navParams: NavParams) {
    let friendRef = this.db.object(`users/${this.navParams.get("friend")}`)
    friendRef.subscribe(data =>{
      if(data !== null){
        this.friend = data;
      }
    });
    
    this.user = this.navParams.get("user");
    this.msgSub = this.db.list('/messages').subscribe(data =>{
      data.map(message =>{
        console.log("message", message);
      });
    });

  }


  goBack(){
    this.navCtrl.setRoot(TabsPage);
  }
  sendMessage(){
    // if(this.msg.trim() != ""){
    //   let messageObj :Message = {
    //     content: this.msg,
    //     friend: this.user,
    //     create_time: "2017-09-04"
    //   }
    //   this.msg = "";
    //   this.messages.push(messageObj);
    // }
    this.db.list("/messages").push({
      content: this.msg,
      from_uid: this.user.uid,
      to_uid: this.friend.uid,
      timestamp: firebase.database.ServerValue.TIMESTAMP
    }).then((message)=>{
      this.messages.push(message);
    });
  }
  

}



